"use client";

import { memo } from 'react';

interface StatCardProps {
  title: string;
  value: number;
  icon: React.ReactNode;
  iconBgColor: string;
  iconColor: string;
  loading?: boolean;
}

// Memoized stat card component
export const StatCard = memo(function StatCard({
  title,
  value,
  icon,
  loading = false,
}: StatCardProps) {
  return (
    <article
      className="group rounded-2xl bg-[#0A0A0A] border border-white/5 p-6 transition-all hover:border-white/10"
      aria-label={`${title}: ${loading ? 'Loading' : value}`}
    >
      <div className="flex items-center justify-between mb-4">
        <div
          className={`flex h-10 w-10 items-center justify-center rounded-lg bg-white/5`}
          aria-hidden="true"
        >
          {/* Force icon to white or neon depending on exact need, but for now allow props or reset to white */}
          <span className={`text-white [&>svg]:h-5 [&>svg]:w-5`}>{icon}</span>
        </div>
        {/* Optional: Add a sparkline or trend indicator here later */}
      </div>

      <div className="min-w-0">
        <p className="text-sm text-gray-400 truncate mb-1" id={`stat-${title.replace(/\s+/g, '-').toLowerCase()}`}>{title}</p>

        {loading ? (
          <div
            className="h-8 w-24 animate-pulse rounded bg-white/10"
            role="status"
            aria-label="Loading value"
          >
            <span className="sr-only">Loading...</span>
          </div>
        ) : (
          <p
            className="text-2xl font-bold text-white tracking-tight"
            aria-labelledby={`stat-${title.replace(/\s+/g, '-').toLowerCase()}`}
          >
            {value}
          </p>
        )}
      </div>
    </article>
  );
});
